<template>
    <div class='datacenter'>
        <div class='center'>
            <p>数据中心机房</p>

            <p class='txt'>
                <span>数据中心(DataCenter)</span>是指在一个物理空间内实现信息的集中处理、存储、传输、交换、管理，而计算机设备、服务器设备、网络设备、存储设备等通常认为是网络核心机房的关键设备。这些关键设备运行所需要的环境因素，如供电系统、制冷系统、机柜系统、消防系统、监控系统等通常被认为是关键物理基础设施。  数据中心机房是一个由多个专业组成的系统工程，它包括了智能建设工程的各个专业，
                <br/>
                <strong>主要包括:</strong> &nbsp;装饰系统；电气系统、接地及防雷系统、空调通风系统、火灾自动报警及自动灭火系统、PDU配电系统、屏蔽工程、综合布线系统、安全防范系统、设备及环境监控系统等。
            </p>
            <img src="/static/img/sjk/1.jpg" alt="">
        </div>
    </div>
</template>
